import {Component, OnInit} from '@angular/core';
import {QuestionsService} from '../shared/questions.service';

@Component({
  selector: 'app-question-list',
  templateUrl: './question-list.component.html',
  styleUrls: ['./question-list.component.css']
})
export class QuestionListComponent implements OnInit {

  questions = [];

  constructor(private questionService: QuestionsService) {
  }

  ngOnInit() {
    this.getQuestions();
  }

  getQuestions() {
    this.questionService.getQuestions().subscribe(res => {
      this.questions = res;
      this.questions.sort(this.sortArray);
    });
  }

  moveUp(question) {
    const i = this.questions.indexOf(question);
    if (i > 0) {
      const up = this.questions[i - 1];
      const down = this.questions[i];
      this.questionService.updateQuestion(up, down, i);
    }
  }

  moveDown(question) {
    const i = this.questions.indexOf(question);
    if (i < this.questions.length - 1) {
      const up = this.questions[i];
      const down = this.questions[i + 1];
      this.questionService.updateQuestion(up, down, i + 1);
    }
  }

  deleteQuestion(question) {
    this.questionService.deleteQUestion(question);
  }

  sortArray(a, b) {
    if (a.payload.doc.data().pos > b.payload.doc.data().pos) {
      return 1;
    }
    if (a.payload.doc.data().pos < b.payload.doc.data().pos) {
      return -1;
    }
    if (a.payload.doc.data().pos === b.payload.doc.data().pos) {
      return 0;
    }
  }
}
