import {Component, OnInit} from '@angular/core';
import {QuestionsService} from '../shared/questions.service';
import {Router} from '@angular/router';
import {AnswerModel} from '../shared/answer.model';

@Component({
  selector: 'app-questions',
  templateUrl: './questions.component.html',
  styleUrls: ['./questions.component.css']
})
export class QuestionsComponent implements OnInit {

  currentQuestion = 0;
  questions = [];
  answers = [];

  constructor(private service: QuestionsService, private router: Router) {
  }

  ngOnInit() {
    this.service.getQuestions().subscribe(val => {
      console.log(val);
      this.questions = val;
      this.questions.sort(this.sortArray);
    });
  }

  addAnswer(a) {
    const answer = {
      question: this.currentQuestion,
      answer: a
    };
    this.answers.push(answer);
    this.currentQuestion++;
    if (this.currentQuestion === this.questions.length) {
      this.uploadAnswers();
      this.router.navigateByUrl('/end');
    }
  }

  uploadAnswers() {
    this.service.uploadAnswers(this.answers).then(val => {
      console.log(val);
    }).catch(e => {
      console.log(e.message);
    });
  }

  sortArray(a, b) {
    if (a.payload.doc.data().pos > b.payload.doc.data().pos) {
      return 1;
    }
    if (a.payload.doc.data().pos < b.payload.doc.data().pos) {
      return -1;
    }
    if (a.payload.doc.data().pos === b.payload.doc.data().pos) {
      return 0;
    }
  }
}
