import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import {QuestionEditorComponent} from './question-editor/question-editor.component';
import { HomeComponent } from './home/home.component';
import {QuestionsComponent} from './questions/questions.component';
import {QuestionEndComponent} from './question-end/question-end.component';
import {QuestionResultsComponent} from './question-results/question-results.component';

const routes: Routes = [
  {path: 'editor', component: QuestionEditorComponent},
  {path: 'questions', component: QuestionsComponent},
  {path: 'home', component: HomeComponent},
  {path: 'end', component: QuestionEndComponent},
  {path: 'result', component: QuestionResultsComponent},
  {path: '', redirectTo: 'home', pathMatch: 'full'},
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
