import {Component, OnInit} from '@angular/core';
import {QuestionsService} from '../shared/questions.service';


@Component({
  selector: 'app-questions-new',
  templateUrl: './questions-new.component.html',
  styleUrls: ['./questions-new.component.css']
})
export class QuestionsNewComponent implements OnInit {

  answer = '';
  color = '';

  answers = [];
  colors = [];

  pos = 0;

  constructor(private questionService: QuestionsService) {
  }

  ngOnInit() {
    this.questionService.getQuestions().subscribe(val => {
      this.pos = val.length;
    });
  }

  addAnswer() {
    if (this.answer !== '') {
      this.answers.push(this.answer);
    }
  }

  addColor() {
    if (this.color !== '') {
      this.colors.push(this.color);
    }
  }

  removeAnswer(answer) {
    const index = this.answers.indexOf(answer);
    if (index > -1) {
      this.answers.splice(index, 1);
    }
  }

  removeColor(color) {
    const index = this.colors.indexOf(color);
    if (index > -1) {
      this.colors.splice(index, 1);
    }
  }

  onSubmit() {
    this.questionService.form.value.colors = this.colors;
    this.questionService.form.value.answers = this.answers;
    this.questionService.form.value.pos = this.pos;
    const data = this.questionService.form.value;

    this.questionService.createQuestion(data)
      .then(res => {
        console.log('Upload');
      });
  }

}
