import {Component, OnInit} from '@angular/core';
import {QuestionsService} from '../shared/questions.service';
import {saveAs} from 'file-saver';
import index from '@angular/cli/lib/cli';

@Component({
  selector: 'app-question-results',
  templateUrl: './question-results.component.html',
  styleUrls: ['./question-results.component.css']
})
export class QuestionResultsComponent implements OnInit {

  answers = [];

  constructor(private  service: QuestionsService) {
  }

  ngOnInit() {
    this.service.getAnswers().subscribe(val => {
      this.answers = val;
    });
  }

  formatAnswers() {
    const a = [];
    let i = 0;
    for (const rows of this.answers) {
      const row = [];
      row.push(i);
      i++;
      for (const answer of rows.payload.doc.data().answers) {
       row.push(answer.answer);
      }
      a.push(row);
    }
    this.downloadFile(a);
  }

  downloadFile(data: any) {
    const replacer = (key, value) => value === null ? '' : value; // specify how you want to handle null values here
    const header = Object.keys(data[0]);
    const csv = data.map(row => header.map(fieldName => JSON.stringify(row[fieldName], replacer)).join(';'));
    csv.unshift(header.join(','));
    const csvArray = csv.join('\r\n');

    const blob = new Blob([csvArray], {type: 'text/csv'});
    saveAs(blob, 'Results.csv');
  }

}
